<template>
  <div class="change-email">
    <div>
      <span class="label">:</span>
      <span>{{ currentEmail }}</span
      >&nbsp;&nbsp;&nbsp;&nbsp;
      <a @click="changeEmail"></a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentEmail: ""
    };
  },
  methods: {
    _changeEmail() {
      this.$http
        .post("/auth/change-email")
        .then(response => {
          this.$message.success(response.data.message);
        })
        .catch(error => {
          this.$message.error(error.response.data.message);
        });
    },
    changeEmail() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(_ => {
          this._changeEmail();
        })
        .catch(_ => {});
    },
    fetchEmail() {
      this.$http
        .get("/user")
        .then(response => {
          this.currentEmail = response.data.email;
        })
        .catch(response => {
          console.log("fetch email error");
        });
    }
  },
  created() {
    this.fetchEmail();
  }
};
</script>

<style lang="less">
.change-email {
  width: 850px;
  margin: 10px auto 0;
  .label {
    font-size: 1.2em;
  }
}
</style>
