<template>
  <div>
    <div class="delete-account">
      <p></p>
      <p></p>
      <br />
      <a @click="open"></a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      password: ""
    };
  },
  methods: {
    open() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        inputPattern: /^[\s\S]*.*[^\s][\s\S]*$/,
        inputErrorMessage: "!",
        inputType: "password"
      })
        .then(({ value }) => {
          this.password = value;
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning"
          })
            .then(_ => {
              this._deleteAccount();
            })
            .catch(_ => {});
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    logout() {
      localStorage.removeItem("isLogin");
      localStorage.removeItem("token");
      this.$router.push("/");
      this.$store.commit("changeLogin", false);
    },
    _deleteAccount() {
      const params = new URLSearchParams();
      params.append("password", this.password);
      this.$http
        .delete("/user", {
          data: params,
          headers: { "Content-Type": "application/x-www-form-urlencoded" }
        })
        .then(response => {
          this.$message.success("!");
          this.logout();
        })
        .catch(error => {
          this.$message.error(" !");
        });
    }
  }
};
</script>

<style lang="less">
.delete-account {
  width: 850px;
  margin: 10px auto 0;
}
</style>
